var Vue = require('vue')
var Index = require('../views/index.vue')
var About = require('../views/about.vue')

// new Vue({
//   el: 'body',
//   components: {
//     app: App2
//   }
// })

var VueRouter = require('vue-router')
Vue.use(VueRouter)
var VueResource = require('vue-resource')
Vue.use(VueResource)

// 
var Foo = Vue.extend({
    template: '<p>This is foo!</p>'
})

var Bar = Vue.extend({
    template: '<p>This is bar!</p>'
})


// 
//  HTML 
var Apptest = Vue.extend({})

// 
// 
var router = new VueRouter()

// 
//  Vue.extend
// 
// 
router.map({
    '/index':{
        component: Index
    },
    '/about': {
        component: About
    },
    '/foo': {
        component: Foo
    },
    '/bar': {
        component: Bar
    }
})

// 
//  App  #app 
router.start(Apptest, '#test')


